<template>
	 <section class="df_jh_df">
                <ul>
            <li v-for="(sd,idx) in caidan" class="sz" :class="index==idx?'act':''" @click="hf(sd.hf)">{{sd.name}}</li>
            </ul>
                    
    
            </section>
</template>
<script>
    export default {
        props: {
            index: ""
        },

        data() {
            return {
                caidan: [{
                    name: "我的任务",
                    hf:'woderenwu'
                }, {
                    name: "进行中的任务",
                    hf: "jinxzdrenwu"
                }, {
                    name: "提交审核的任务",
                    hf: "tijiaorenwu"
                }, {
                    name: "已完成的任务",
                    hf: "yiwanwc"
                }, ]
            }
        },
        components: {

        },
        methods: {

        },
        mounted() {

        },
    }

</script>
<style scoped>
    .df_jh_df {
        width: 180px;
        float: left;

    }

    .df_jh_df li {
        padding-left: 40px;
        font-size: 15px;
        color: #606060;
        line-height: 60px;
    }


    .df_jh_df li.act,
    .df_jh_df li:hover {
        color: #0099F6;
        background: #F8F8F8;
    }

</style>



